import React from 'react'
import PageTemplate from '../page-template'

import {getMenuItems, PagesDetails} from '../common'

const RENDER_PAGE = 'retail-car'

const Car = () => {
    const {subMenu} = PagesDetails.retail
    const menu = getMenuItems(Object.values(subMenu), RENDER_PAGE)

    return <div>
        <PageTemplate {...subMenu[RENDER_PAGE]} menu={menu}/>
    </div>
}

export default Car
