import React from 'react'
import Helmet from "../component/common/Helmet";
import Header from "../component/header/Header";
import PortfolioMasonry from "../elements/portfolio/PortfolioMasonry";
import HeadingDescription from "./heading-description";
import Block from "./block";

const pageTemplate = ({menu = [], blocksData, blocksBreakpoints, label, desc, id}) => {

    return (<div className="active-dark bg_color--10 full-height">
        <Helmet pageTitle={label}/>

        {/* Start Header Area  */}
        <Header menuItems={menu}/>
        {/* End Header Area  */}

        {/* Start portfolio Area  */}
        <div id={id} key={id} className="rn-portfolio-area bg_color--10 ptb--120">
            <HeadingDescription title={label} description={desc}/>
            <div className="wrapper plr--30">
                {
                    blocksData.map(({blockDesc, blocks}) => {
                        return (
                            <React.Fragment>
                                {blockDesc && <HeadingDescription {...blockDesc} />}
                                <div className="row">
                                    {
                                        blocks.map(value => <Block value={value} key={JSON.stringify(value)}/>)
                                    }
                                </div>
                            </React.Fragment>
                        )
                    })
                }
            </div>
        </div>
        {/* End portfolio Area  */}
    </div>)
}

export default pageTemplate