import React, {Component, Fragment} from "react";
import Header from "../component/header/Header";
import Helmet from "../component/common/Helmet";

import {PortfolioList} from '../pages/common'

const SlideList = [
    {
        textPosition: 'text-center',
        category: '',
        title: 'Minimal',
        description: 'There are many variations of passages of Lorem Ipsum available but the majority have suffered alteration.',
        buttonText: 'Contact Us',
        buttonLink: '/contact'
    }
]

class HomePortfolio extends Component {
    constructor() {
        super()
        this.state = {
            isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }

    openModal() {
        this.setState({isOpen: true})
    }

    getMenuItems() {
        return PortfolioList.map(({id, label, subMenu}) => ({
            id,
            label,
            subMenu: Object.values(subMenu)
        }))
    }

    render() {
        const menuItems = this.getMenuItems()

        return (
            <div className="active-dark bg_color--10 full-height">
                <Helmet pageTitle="Home Portfolio"/>

                {/* Start Header Area  */}
                <Header menuItems={menuItems}/>
                {/* End Header Area  */}
                {/* Start Slider Area   */}

                <div className="slider-wrapper">
                    {/* Start Single Slide */}
                    {SlideList.map((value, index) => (
                        <div
                            className="slide slide-style-2 fullscreen d-flex align-items-center justify-content-center bg_image bg_image--24"
                            key={index} data-black-overlay="6">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className={`inner ${value.textPosition}`}>
                                            {value.category ? <span>{value.category}</span> : ''}
                                            {value.title ? <h1 className="title theme-gradient">{value.title}</h1> : ''}
                                            {value.description ?
                                                <p className="description">{value.description}</p> : ''}
                                            {value.buttonText ? <div className="slide-btn"><a
                                                className="btn-default btn-border btn-opacity"
                                                href={`${value.buttonLink}`}>{value.buttonText}</a></div> : ''}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
                {/* End Slider Area   */}
            </div>
        )
    }
}

export default HomePortfolio;